// Grid of people for the /people index pages, fellows listing etc

.people-grid {
  @include list-unformatted;
  @include block;

  li {
    display: flex;
    padding: $baseline / 2 0; }
  .title {
    min-width: 10%;
    @include set-property-to-gutter(margin-right);
    & {
      font-size: 1.5em;
      @include respond-up(tab-port) {
        font-size: 2.5em; }
      letter-spacing: -1px; }
    &, a {
      font-weight: $font-weight-light;
      color: $color-text-accent; }
    a {
      @include no-underline(true); } }

  .people-grid-list {
    flex: 1; } }

@mixin people-grid-list-scale($scale) {
  li {
    padding: $baseline / 2;

    max-width: ($baseline + 160px) / $scale;

    @include respond-up(mob-port) {
      width: 33% / $scale; }
    @include respond-up(tab-port) {
      width: 25% / $scale; }
    @include respond-up(tab-land) {
      width: 25% / $scale; }
    @include respond-up(med-desk) {
      width: 16% / $scale; } } }


.people-grid-list {
  @include list-unformatted;
  display: flex;
  flex-wrap: wrap;

  @include people-grid-list-scale(1);

  li {
    @include set-property-to-gutter(padding-right);

    a {
      width: 100%; } }

  .name {
    text-align: center;
    color: $color-text-white;
    font-size: 0.9em;
    font-weight: normal;
    word-wrap: break-word; }

  .person-headshot-actual, .person-headshot-placeholder {
    width: 100%;
    border-radius: 999px; }

  .person-headshot-placeholder {
    opacity: 0.2; } }

.people-grid-scroller {
  position: fixed;
  z-index: $zindex-content-toolbar;
  left: 0;
  bottom: 0;
  width: 100%;
  background: mix($color-nnt-purple, black, 20%);

  .inner {
    @include grid-wrap; }

  // .scroller
