@charset "utf-8";

// Our variables
$body-font-stack: 'Open Sans', Helvetica, sans-serif;
$header-font-stack: 'Montserrat', sans-serif;
$montserrat-letter-spacing: -0.7px;
$base-font-size: 17px;
$base-font-size-compact: $base-font-size * 0.875;
$baseline-multiplier: 1.6;
$baseline: $base-font-size * $baseline-multiplier;
$font-weight-bold: 600;
$font-weight-light: 300;

$color-nnt-purple: #A54499;
$color-nnt-orange: #FFC425;
$color-brand-alt: #2D9189;

$color-black: #1c1c1c;

$color-text-white: #eee;
$color-text: $color-text-white;
$color-text-black: #111;
$color-text-accent: mix($color-nnt-purple, $color-text-white);

$color-white: #f1f1f1;

$color-background: darken($color-nnt-purple, 40%);
$color-background-alt: darken($color-nnt-purple, 23%);

$color-link: #FFC425;
$color-link-visited: darken($color-link, 07%);
$color-link-bg-hover: darken($color-nnt-purple, 20%);
$color-focus: $color-text-white;
$color-decades-base: #601111;

$color-error: #601111;
$color-warning: #864116;
$color-success: #116019;
$color-info: #114560;

$color-grey: #828282;
$color-grey-light: lighten($color-grey, 40%);
$color-grey-dark: darken($color-grey, 25%);

$color-selection: $color-nnt-orange;

$color-empty-poster: mix($color-black, $color-background, 90%);

$color-venues: #4D8E13;
$color-seasons: #134466;
$color-playwright: #9F3B15;

$color-debug: #4FFF1F;
$color-debug-attr-base: #1e8c10;
$color-debug-attr-required: #E56818;
$color-debug-attr-generated: #0b5e92;

$element-transition: 0.34s ease-out;

$grid-transition-enable: false;
$grid-column-count: 16;
$grid-row-padding: $baseline;
$grid-column-start: "tab-port";
$grid-column-end: "mob-land";

$zindex-content-toolbar: 40;
$zindex-header: 50;
$zindex-footer: 50;
$zindex-sticky: 480;
$zindex-report-tab: 500;
$zindex-modal-bg: 990;
$zindex-search-overlay: 1000;
$zindex-modal: 1000;
$zindex-debug: 9000;

