// Homepage

$golden: 61.8%;
$golden-counterpart: 100% - $golden;

$section-colour-one: #000;
$section-colour-two: #115760;
$section-colour-tre: #601123;

.home-new-banner {
  background: #E91818;
  color: #fff;
  font-weight: $font-weight-bold;
  padding: $baseline / 2;
  text-align: center; }

.home-info {
  // Container of words
  h2 {
    font-weight: normal;
    font-size: $base-font-size * 3.2;
    line-height: $baseline * 3; }

  p {
    font-size: $base-font-size * 1.44;
    line-height: $baseline * 2; } }

.home-shows {
  @include grid-row;

  background: $section-colour-one;

  overflow: hidden;

  .home-shows-design-element {
    @include grid-x-percent($golden);
    position: relative;
    min-height: 300px;

    @include respond-down(mob-land) {
      // Doesn't adapt well for mobile, hide for now
      display: none; }

    .posterwall {
      // position: absolute
      padding-right: $baseline * 3; }

    .posterwall-bodge {
      width: 90%; } }

  .home-info {
    @include grid-x-percent($golden-counterpart);
    @include respond-up(widescreen) {
      padding-top: $baseline; } } }

.home-network, .home-contribute {
  @include grid-wrap;

  @include respond-down(mob-land) {
    @include set-property-to-gutter(padding-left);
    @include set-property-to-gutter(padding-right); }

  @include respond-up(tab-port) {
    // Slight bodge to make full width cols
    float: left;
    width: 50%;
    min-height: 33 * $baseline; }

  @include respond-up(tab-land) {
    min-height: 28 * $baseline; }

  @include respond-up(med-desk) {
    min-height: 23 * $baseline; } }


$home-section-inner-padding: $baseline * 2;

.home-network {
  background: $section-colour-two;

  @include respond-up(tab-port) {
    // Inner padding
    padding-right: $home-section-inner-padding; }

  h2 {
    @include text-shadow-effect($section-colour-two); } }



.home-contribute {
  background: $section-colour-tre;

  @include respond-up(tab-port) {
    // Inner padding
    padding-left: $home-section-inner-padding; }

  h2 {
    @include text-shadow-effect($section-colour-tre); } }


.home-contribute-actions {
  display: flex;
  @include block; }

.home-contribute-action {
  position: relative;
  flex-basis: 50%;
  padding: $baseline / 2;
  margin-right: 8px;

  background: $color-text-black;

  &:hover {
    background: lighten($color-text-black, 3%); }

  overflow: hidden;

  &:last-child {
    margin-right: 0; }

  i {
    position: absolute;
    bottom: -60px;
    right: -2px;
    font-size: 152px;
    color: $color-text-white;
    opacity: 0.1; }

  a {
    color: $color-text-white;

    h3, p {
      font-size: $base-font-size;
      line-height: $baseline;
      text-align: center; }

    h3 {
      margin-bottom: $baseline / 2; }

    @include no-underline;

    &:hover {
      h3 {
        text-decoration: underline; } } } }

// Overrides

.home-section {
  // High level block on the home page
  padding-top: $baseline;
  padding-bottom: $baseline;
  @include respond-up(tab-port) {
    padding-top: $baseline * 2;
    padding-bottom: $baseline * 2; } }

.home-button {
  width: 100%;
  a {
    display: block;
    width: 100%;
    background: $color-text-black;
    padding: $baseline / 4 $baseline / 2;
    color: $color-text-white;
    @include no-underline(true);

    &:hover {
      background: lighten($color-text-black, 4%); } } }

.home-shows-counter-container {
  p {
    padding-left: 5px; } }

.home-shows-counter {
  $char-width: 43px;
  $char-tab-land: 38px;
  $char-tab-port: 31px;

  position: relative;
  overflow: hidden;

  // width: $char-width * 3
  font-size: $base-font-size * 3;
  font-family: 'Open Sans', Helvetica, sans-serif;
  color: #fff;
  line-height: $baseline * 5;
  line-height: 1em;

  .char1, .char2, .char3, .char4 {
    background: $color-black;
    width: $char-width;
    text-align: center;
    display: block;
    float: left;
    padding: 5px 0;

    border-radius: 6px;

    margin-left: 2px; }

  @include respond-down(tab-land) {
    .char1, .char2, .char3, .char4 {
      width: $char-tab-land; } }
  @include respond-down(tab-port) {
    font-size: $base-font-size * 2.5;
    .char1, .char2, .char3, .char4 {
      width: $char-tab-port; } } }

.home .site-content {
  overflow: hidden;
  margin-bottom: 0; }
